





























import { Component, Vue } from 'vue-property-decorator';
import axios from 'axios';

@Component({
})
export default class Home extends Vue {
    userName = "";
    content = "";
    todoList: any[] = [];
    type = "today";
    mounted () {
        axios.get('/ajax/getUserInfo')
        .then((data: any) => {
            this.userName = data.data.name;
            if (!this.userName) {
                this.$router.push('/app/login');
            }
        });
        this.getTodoList();
    }
    getTodoList() {
        axios.get('/ajax/getToDoList', {
            params: {
                type: this.type,
            }
        })
        .then((data: any) => {
            this.todoList = data.data.data.todoList;
        })
    }
    toLogin() {
        this.$router.push('/app/login?type=login');
    }
    toRegister() {
        this.$router.push('/app/login?type=register');
    }
    addTodo() {
        axios.post('/ajax/addTodo', {
            content: this.content,
            type: this.type,
        })
        .then((data: any) => {
            this.todoList.push(data.data.data.todo);
            this.content = '';
        });
    }
    changeType(type) {
        if (this.type === type) {
            return;
        }
        this.type = type;
        this.getTodoList();
    }
}
